body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
  position: relative;
}

@font-face {
  font-family: 'SEGA LOGO FONT';
  font-style: normal;
  font-weight: normal;
  src: local('SEGA LOGO FONT'), url(/robofriends/static/media/SEGA.12a3db8b.woff) format('woff');
}

h1 {
  font-family: 'SEGA LOGO FONT';
  font-weight: 200;
  color: #0ccac4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

div {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.main div {
  flex-direction: column;
}
.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 20vh;
  font-size: 2vmax;
  width: 100%;
}

@media only screen and (orientation: landscape) {
  .App-header {
    position: relative;
    flex-direction: row;
    -webkit-margin-start: 0.68em;
            margin-inline-start: 0.68em;
  }

  .h1 {
    left: 0;
  }
}

input {
    padding: 2vh;
    background: transparent;
    font-size: inherit;
    font-family: inherit;
    font-weight: inherit;
    color: inherit;
    -webkit-appearance: none;
}

.box {
    padding: 2vh;
}

@media only screen and (orientation: landscape) {
    .box {
        position: absolute;
        right: 0;
    }
  }

